<template>
    <div class="loading">
      <img src="./loading.gif" width="24" height="24" alt="">
      <p class="desc">{{title}}</p>
    </div>
</template>

<script type="es6">
    export default {
      props:{
        title:{
          type:String,
          default:"正在载入...."
        }
      }
    }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus" type="text/stylus">
  @import "~common/stylus/variable"

  .loading
    width: 100%
    text-align: center
    .desc
      line-height:20px
      font-size: $font-size-small
      color: $color-text-1
</style>



