<template>
  <div id="app">
    <v-header></v-header>
    <v-tab></v-tab>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <player></player>
  </div>
</template>

<script type="es6">
  import vHeader from 'components/m-header/m-header';
  import vTab from 'components/tab/tab';
  import player from 'components/player/player';
  export default {
    name: 'App',
    components:{
      vHeader,
      vTab,
      player
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" type="text/stylus">

